<template>
  <div class="shops-list">
      <div class="div-empty">
        <img onclick="return false" class="img" src="../../../assets/aml_logo.png"/>
        <p class="empty-tip">敬请期待~</p> 
      </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "shops-list",
  data() {
    return {
      brandData: null,
      isLoadDb: false,
      showEmpty: false
    };
  },
  components: {},
  activated() {
    document.body.scrollTop = 0;
    if (this.isLoadDb) this.$store.dispatch("toggleLoading", false);
  },
  mounted() {
    this.loadData();
  },
  beforeRouteLeave(to, from, next) {
    next();
  },
  methods: {
    loadData() {
      this.isLoadDb = true;
      this.$store.dispatch("toggleLoading", false);
      this.showEmpty = true;
    }
  },
  computed: {
    ...mapState(["openid", "path", "userid"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.shops-list {
  width: 100%;
  flex: 1;
  position: relative;
  display flex;
  padding-bottom: 0.43rem;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;

  .div-empty {

    .img {
      width: 1.7rem;
      height: auto;
    }
    .empty-tip{
      margin-top .12rem;
      font-size .15rem;
      text-align  center;
      color #a2733d;
      letter-spacing 0.01rem;
    }
  }
}
</style>



